@charset "UTF-8";
.hm_btn {
  display: block;
  position: fixed;
  background-color: rgb(238, 238, 238);
  cursor: pointer;
  top: 20px;
  right: 20px;
  width: 50px;
  padding: 25px 0;
  z-index: 3;
  /* ページロード時アニメーション ここから*/
  /* ページロード時アニメーション ここまで*/
  /* ボタン押下時アニメーション ここから*/
  /* ボタン押下時アニメーション ここまで*/
}
.hm_btn span[class^=btn_line] {
  display: block;
  position: absolute;
  right: 0;
  left: 0;
  margin-right: auto;
  margin-left: auto;
  background-color: #333;
  width: 60%;
  height: 1px;
}
.hm_btn .btn_line_top {
  top: 30%;
}
.hm_btn .btn_line_middle {
  top: 0;
  bottom: 0;
  margin-top: auto;
  margin-bottom: auto;
}
.hm_btn .btn_line_bottom {
  bottom: 30%;
}
.hm_btn .btn_line_top {
  animation: btn_line_top 0.75s forwards;
}
@keyframes btn_line_top {
  0% {
    transform: rotate(45deg);
    top: calc(50% - (1px / 2));
  }
  50% {
    transform: rotate(0);
    top: calc(50% - (1px / 2));
  }
  100% {
    transform: rotate(0);
    top: 30%;
  }
}
.hm_btn .menu_btn_middle {
  transition: all 0.25s 0.25s;
  opacity: 1;
}
.hm_btn .btn_line_bottom {
  animation: btn_line_bottom 0.75s forwards;
}
@keyframes btn_line_bottom {
  0% {
    transform: rotate(-45deg);
    bottom: calc(50% - (1px / 2));
  }
  50% {
    transform: rotate(0);
    bottom: calc(50% - (1px / 2));
  }
  100% {
    transform: rotate(0);
    bottom: 30%;
  }
}
.hm_btn.is_opened .btn_line_top {
  animation: opened_btn_line_top 0.75s forwards;
}
@keyframes opened_btn_line_top {
  0% {
    transform: rotate(0);
    top: 30%;
  }
  50% {
    transform: rotate(0);
    top: calc(50% - (1px / 2));
  }
  100% {
    transform: rotate(45deg);
    top: calc(50% - (1px / 2));
  }
}
.hm_btn.is_opened .btn_line_middle {
  opacity: 0;
}
.hm_btn.is_opened .btn_line_bottom {
  animation: opened_btn_line_bottom 0.75s forwards;
}
@keyframes opened_btn_line_bottom {
  0% {
    transform: rotate(0);
    bottom: 30%;
  }
  50% {
    transform: rotate(0);
    bottom: calc(50% - (1px / 2));
  }
  100% {
    transform: rotate(-45deg);
    bottom: calc(50% - (1px / 2));
  }
}

/* ハンバーガーメニューボタン ここまで*/
#wrapper {
  height: 3000px;
}

.container {
  max-width: 1300px;
  margin-right: auto;
  margin-left: auto;
}
.container .content_width {
  width: calc(100% - 40px);
  margin-right: auto;
  margin-left: auto;
}

#hm_canvas .canvas_off {
  background-color: rgba(51, 51, 51, 0.7);
}
#hm_canvas .canvas_body {
  padding: 1em 0;
  width: 80%;
  height: 100vh;
  background-color: #ccc;
}

#hamburger_menu_canvas .body_menu .menu_animation_item {
  transform: translateX(-200px);
  transition: all 0.2s;
  opacity: 0;
}
#hamburger_menu_canvas.is_opened .body_menu ul li {
  transform: translateX(0);
  opacity: 1;
}